import React from "react";
import { NavLink } from "react-router-dom";

// The Header creates links that can be used to navigate
// between routes.
const Header = () => (
  <header>
    <nav className="navbar navbar-expand-lg">
      <div className="mr-auto col-sm-12 col-md pl-0" id="navbarNav">
        <img
          src="/triforcecoin.png"
          width="30"
          height="30"
          className="d-inline-block align-top mr-2 float-left"
          alt=""
        />
        <ul className="navbar-nav">
          <NavLink className="nav-link" exact to="/">Active Coins</NavLink>
          <NavLink className="nav-link" to="/inactive">Inactive Coins</NavLink>
        </ul>
      </div>
      <div className="ml-md-auto mt-md-auto mb-md-auto p-2 mr-md-2 well mt-2 mt-md-0">
        <small>
          <em>Featured coin:</em>&nbsp;&nbsp;<a href="https://discord.gg/UGrBXEt" target="_blank" title="Aeon Classic Discord"><img src="/logos/aeonclassic-icon.png" height="18" className="align-text-bottom" /> Aeon Classic</a>
        </small>
      </div>
      <div className="header-links">
        <div className="row m-0">
          <div className="col-6 px-2">
            <a
              href="https://discord.gg/h6kA2JS"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-discord" />
            </a>
          </div>
          <div className="col-6 px-2">
            <a
              href="https://twitter.com/triforcecoin"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-twitter" />
            </a>
          </div>
        </div>
      </div>
    </nav>
  </header>
);

export default Header;
